.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: radial-gradient(circle at bottom right, #2E004A, #0D0221 60%);
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    z-index: 1000;
    transition: all 0.3s ease-out;
}
.navbar-sign {
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
    margin-left: 1cm;
    justify-content: center;
}

.navbar-menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 3rem;
    flex: 1;/* Allows it to take up space between navbar-sign and GitHub button */
    justify-content: right;
    text-align: right;
    margin-right: 1cm;
}

.navbar-menu li {
    display: inline;
}

.navbar-menu a {
    color: white;
    font-size: 1rem;
    text-decoration: none;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
}
.button-container {
    margin-right: 0.3cm;
    background: radial-gradient(circle at bottom right, #2E004A, #0D0221 60%);
    border: none;
    box-shadow: none;
    background: none;
}
/* GitHub Button */
.github-button {
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
    background-color: purple;
    border-radius: 5px;
    margin-right: 2cm;
    transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    margin-left: auto; /* Pushes the GitHub button to the far right */
    margin-right: 10px; /* Adds space between the GitHub button and the right border of the navbar */
}
.github-button a{
    font-size: 20px;
}

.github-button:hover {
    background-color: purple;
    transform: translateX(-5px);
    opacity: 0.8;
}

/* Toggle button styling */
.navbar-toggle {
    display: none; /* Hidden by default */
    font-size: 1.5rem;
    background: none;
    margin-right: 1rem;
    border: none;
    color: white;
    cursor: pointer;
}

/* Link underline on hover */
.navbar ul li a {
    position: relative;
}

.navbar ul li a::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    margin-top: 1rem;
    border-radius: 25px;
    height: 5px;
    background-color: purple;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
    transform-origin: right;
}

.navbar ul li a:hover::after {
    transform: scaleX(1);
    transform-origin: left;
}

/* Responsive Design */
@media (max-width: 768px) {
    .navbar {
        flex-direction: column;
        display: flexbox;
        display: block;
    }
    .navbar-menu.active .github-button {
        display: flex; /* Show GitHub button inside menu */
        justify-content: center;
        padding: 10px;
        margin: 1rem 0; /* Optional spacing */
    }
    .navbar-menu {
        display: none; /* Hide menu by default */
        flex-direction: column;
        position: absolute;
        top: 60px;
        left: 0;
        gap:2rem;
        width: 50%;
        background-color: #333;
        margin: 0;
    }
    .navbar ul li a::after {
        width: 50%;
    }
    .navbar-menu.active {
        display: flex; /* Show the menu when active */
    }

    .navbar-menu li {
        text-align: center;
        padding: 10px;
    }

    .github-button {
        margin: 1rem auto; /* Center GitHub button on small screens */
        width: 50%;
    }
    /* Show toggle button on small screens */
    .navbar-toggle {
        display: block;
margin-left: 1cm;
    }
}

